import { Check, CheckCircle } from "lucide-react";
import { useState } from "react";

interface PackageCardProps {
  subscriptionPackages: any;
  curruntPackageid: any;
  startDate?: string;
  expireDate?: string;

  onUpgrade: (data: {
    id: any;
    name: string;
    price: number;
    billingType: "monthly" | "yearly";
  }) => void;
}

const PackageCard: React.FC<PackageCardProps> = ({
  subscriptionPackages,
  curruntPackageid,
  startDate = "Jan 01, 2024",
  expireDate = "Dec 31, 2024",
  onUpgrade,
}) => {
  const [isYearly, setIsYearly] = useState<boolean>(false);

  const currentPackage = subscriptionPackages.find(
    (pkg: any) => pkg.id === curruntPackageid
  );

  const otherPackages = subscriptionPackages.filter(
    (pkg: any) => pkg.id !== curruntPackageid
  );

  const getPackageColors = (packageName: string) => {
    const isGold = packageName === "Gold";
    const isSilver = packageName === "Silver";

    return {
      gradient: isGold
        ? "from-amber-950 via-yellow-900 to-amber-950"
        : isSilver
        ? "from-slate-900 via-gray-900 to-slate-900"
        : "from-slate-900 via-purple-950 to-slate-900",
      border: isGold
        ? "border-amber-500/20"
        : isSilver
        ? "border-gray-500/20"
        : "border-purple-500/20",
      hoverBorder: isGold
        ? "hover:border-amber-400/50"
        : isSilver
        ? "hover:border-gray-400/50"
        : "hover:border-purple-400/50",
      hoverShadow: isGold
        ? "hover:shadow-amber-500/30"
        : isSilver
        ? "hover:shadow-gray-500/30"
        : "hover:shadow-purple-500/30",
      textGradient: isGold
        ? "from-amber-300 via-yellow-300 to-amber-400"
        : isSilver
        ? "from-gray-300 via-slate-300 to-gray-400"
        : "from-purple-300 via-violet-300 to-purple-400",
      bgOverlay: isGold
        ? "from-amber-600/10 via-yellow-600/10 to-amber-800/10"
        : isSilver
        ? "from-gray-600/10 via-slate-600/10 to-gray-800/10"
        : "from-purple-600/10 via-violet-600/10 to-purple-800/10",
      buttonGradient: isGold
        ? "from-amber-600 to-yellow-600 hover:from-amber-500 hover:to-yellow-500"
        : isSilver
        ? "from-gray-600 to-slate-600 hover:from-gray-500 hover:to-slate-500"
        : "from-purple-600 to-violet-600 hover:from-purple-500 hover:to-violet-500",
      accentColor: isGold
        ? "text-amber-400"
        : isSilver
        ? "text-gray-400"
        : "text-purple-400",
      glowColor: isGold
        ? "bg-amber-400/20"
        : isSilver
        ? "bg-gray-400/20"
        : "bg-purple-400/20",
    };
  };

  if (!currentPackage) return null;

  const currentColors = getPackageColors(currentPackage.name);

  return (
    <>
      <div
        className={`relative flex flex-col border ${currentColors.border} py-16 px-5 gap-6 text-center justify-center items-center my-10 rounded-3xl overflow-hidden bg-linear-to-br ${currentColors.gradient} shadow-2xl ${currentColors.hoverShadow} transition-all duration-300`}
      >
        <div
          className={`absolute inset-0 bg-linear-to-br ${currentColors.bgOverlay} opacity-50`}
        />
        <div
          className="absolute inset-0 opacity-30"
          style={{
            backgroundImage: `
              linear-gradient(rgba(192, 192, 192, 0.05) 1px, transparent 1px),
              linear-gradient(90deg, rgba(192, 192, 192, 0.05) 1px, transparent 1px)
            `,
            backgroundSize: "50px 50px",
          }}
        />

        <div
          className={`absolute top-1/4 left-1/4 w-64 h-64 ${currentColors.glowColor} rounded-full blur-3xl animate-pulse`}
        />
        <div
          className={`absolute bottom-1/4 right-1/4 w-64 h-64 ${currentColors.glowColor} rounded-full blur-3xl animate-pulse`}
          style={{ animationDelay: "1s" }}
        />

        <div className="relative z-10 group">
          <div
            className={`absolute inset-0 bg-linear-to-r ${
              currentPackage.name === "Gold"
                ? "from-amber-400 to-yellow-500"
                : currentPackage.name === "Silver"
                ? "from-gray-400 to-slate-500"
                : "from-purple-400 to-violet-500"
            } rounded-full blur-xl opacity-50 group-hover:opacity-75 transition-opacity`}
          />
          <div
            className={`relative h-24 w-24 rounded-full flex items-center justify-center bg-linear-to-br ${
              currentPackage.name === "Gold"
                ? "from-amber-700 to-yellow-800 border-amber-600"
                : currentPackage.name === "Silver"
                ? "from-gray-700 to-slate-800 border-gray-600"
                : "from-purple-700 to-violet-800 border-purple-600"
            } border-2 text-white font-bold text-5xl shadow-xl`}
          >
            {currentPackage.name.charAt(0)}
          </div>
        </div>

        <h1
          className={`relative z-10 text-5xl font-bold bg-linear-to-r ${currentColors.textGradient} bg-clip-text text-transparent drop-shadow-lg`}
        >
          You're on the {currentPackage.name} Plan
        </h1>

        <p className="relative z-10 max-w-2xl px-8 text-gray-300 text-lg leading-relaxed">
          Thanks for subscribing to the {currentPackage.name} Plan. As a part of
          this plan, you have access to:
        </p>

        {expireDate && startDate && (
          <div className="relative z-10 flex gap-8 text-gray-300">
            <div className="text-center">
              <p className="text-sm text-gray-400 mb-1">Start Date</p>
              <p
                className={`text-lg font-semibold ${currentColors.accentColor}`}
              >
                {startDate}
              </p>
            </div>
            <div className="text-center">
              <p className="text-sm text-gray-400 mb-1">Expire Date</p>
              <p
                className={`text-lg font-semibold ${currentColors.accentColor}`}
              >
                {expireDate}
              </p>
            </div>
          </div>
        )}

        <div className="relative z-10 grid grid-cols-1 md:grid-cols-2 gap-4 w-full max-w-2xl px-4">
          {currentPackage.features.map((feature: any, idx: any) => (
            <div
              key={idx}
              className={`group relative bg-linear-to-br ${
                currentPackage.name === "Gold"
                  ? "from-amber-800/80 to-yellow-900/80 border-amber-600/50 hover:border-amber-400/50 hover:shadow-amber-400/20"
                  : currentPackage.name === "Silver"
                  ? "from-gray-800/80 to-slate-900/80 border-gray-600/50 hover:border-gray-400/50 hover:shadow-gray-400/20"
                  : "from-purple-800/80 to-violet-900/80 border-purple-600/50 hover:border-purple-400/50 hover:shadow-purple-400/20"
              } backdrop-blur-sm py-4 px-6 rounded-xl flex items-center gap-3 border transition-all duration-300 shadow-lg hover:scale-105`}
            >
              <div
                className={`absolute inset-0 bg-linear-to-r ${
                  currentPackage.name === "Gold"
                    ? "from-amber-500/10 to-yellow-400/10"
                    : currentPackage.name === "Silver"
                    ? "from-gray-500/10 to-slate-400/10"
                    : "from-purple-500/10 to-violet-400/10"
                } opacity-0 group-hover:opacity-100 transition-opacity rounded-xl`}
              />
              <CheckCircle
                className={`relative z-10 ${currentColors.accentColor} shrink-0`}
              />
              <span className="relative z-10 text-white font-medium">
                {feature}
              </span>
            </div>
          ))}
        </div>
      </div>

      {/* Other Packages */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {otherPackages.map((pkg: any) => {
          const colors = getPackageColors(pkg.name);
          const isGold = pkg.name === "Gold";
          const isSilver = pkg.name === "Silver";

          return (
            <div
              key={pkg.id}
              className={`group relative flex flex-col p-6 rounded-2xl overflow-hidden bg-linear-to-br ${colors.gradient} border ${colors.border} ${colors.hoverBorder} transition-all duration-300 hover:scale-105 shadow-xl ${colors.hoverShadow}`}
            >
              <div
                className={`absolute inset-0 bg-linear-to-br ${colors.bgOverlay} opacity-0 group-hover:opacity-100 transition-opacity duration-500`}
              />

              <div
                className="absolute inset-0 opacity-20"
                style={
                  isGold
                    ? {
                        backgroundImage: `
                          linear-gradient(rgba(251, 191, 36, 0.1) 1px, transparent 1px),
                          linear-gradient(90deg, rgba(251, 191, 36, 0.1) 1px, transparent 1px)
                        `,
                        backgroundSize: "30px 30px",
                      }
                    : isSilver
                    ? {
                        backgroundImage: `
                          linear-gradient(rgba(156, 163, 175, 0.1) 1px, transparent 1px),
                          linear-gradient(90deg, rgba(156, 163, 175, 0.1) 1px, transparent 1px)
                        `,
                        backgroundSize: "30px 30px",
                      }
                    : {
                        backgroundImage: `
                          linear-gradient(rgba(168, 85, 247, 0.1) 1px, transparent 1px),
                          linear-gradient(90deg, rgba(168, 85, 247, 0.1) 1px, transparent 1px)
                        `,
                        backgroundSize: "30px 30px",
                      }
                }
              />

              <div className="relative z-10 flex flex-col gap-4">
                <div className="flex items-center gap-3 mb-2">
                  <div className="flex-1">
                    <h4
                      className={`text-2xl font-bold bg-linear-to-r ${colors.textGradient} bg-clip-text text-transparent`}
                    >
                      {pkg.name}
                    </h4>
                  </div>
                </div>

                {!isSilver && (
                  <div className="flex items-center justify-center gap-3 my-2">
                    <span
                      className={`text-sm font-medium transition-colors ${
                        !isYearly ? "text-white" : "text-gray-500"
                      }`}
                    >
                      Monthly
                    </span>
                    <button
                      onClick={() => setIsYearly(!isYearly)}
                      className={`relative w-14 h-7 ${
                        isGold
                          ? "bg-amber-900/50 border border-amber-500/30 hover:bg-amber-800/50"
                          : "bg-purple-900/50 border border-purple-500/30 hover:bg-purple-800/50"
                      } rounded-full transition-colors`}
                    >
                      <div
                        className={`absolute top-1 left-1 w-5 h-5 bg-linear-to-r ${
                          isGold
                            ? "from-amber-400 to-yellow-500"
                            : "from-purple-400 to-violet-500"
                        } rounded-full shadow-lg transition-transform duration-300 ${
                          isYearly ? "translate-x-7" : "translate-x-0"
                        }`}
                      />
                    </button>
                    <span
                      className={`text-sm font-medium transition-colors ${
                        isYearly ? "text-white" : "text-gray-500"
                      }`}
                    >
                      Yearly
                      <span className={`ml-1 text-xs ${colors.accentColor}`}>
                        (Save 20%)
                      </span>
                    </span>
                  </div>
                )}

                <div className="text-center">
                  <h5 className="text-4xl font-bold text-white">
                    {isSilver ? (
                      "Free"
                    ) : (
                      <>
                        <span className="text-2xl">Rs.</span>{" "}
                        {isYearly ? pkg.yearly_price : pkg.monthly_price}
                      </>
                    )}
                    {!isSilver && (
                      <span className="text-lg text-gray-400">
                        /{isYearly ? "year" : "month"}
                      </span>
                    )}
                  </h5>
                  {isYearly && !isSilver && (
                    <p className={`text-sm mt-1 ${colors.accentColor}`}>
                      Rs. {(pkg.yearly_price / 12).toFixed(0)}/month billed
                      annually
                    </p>
                  )}
                </div>

                <ul className="space-y-3 mt-4">
                  {pkg.features.map((feature: any, idx: any) => (
                    <li
                      key={idx}
                      className="flex items-start gap-3 text-gray-200"
                    >
                      <div
                        className={`mt-1 shrink-0 h-5 w-5 rounded-full ${
                          isGold
                            ? "bg-amber-500/20"
                            : isSilver
                            ? "bg-gray-500/20"
                            : "bg-purple-500/20"
                        } flex items-center justify-center`}
                      >
                        <Check className={`h-3 w-3 ${colors.accentColor}`} />
                      </div>
                      <span className="text-sm">{feature}</span>
                    </li>
                  ))}
                </ul>

                <button
                  onClick={() =>
                    onUpgrade({
                      id: pkg.id,
                      name: pkg.name,
                      price: isYearly ? pkg.yearly_price : pkg.monthly_price,
                      billingType: isYearly ? "yearly" : "monthly",
                    })
                  }
                  className={`mt-6 w-full py-3 px-4 rounded-xl bg-linear-to-r ${colors.buttonGradient} text-white font-semibold shadow-lg transition-all duration-300 hover:scale-105`}
                >
                  Upgrade to {pkg.name}
                </button>
              </div>
            </div>
          );
        })}
      </div>
    </>
  );
};

export default PackageCard;
